// NFT link parser utility
// Parses NFT links in format: t.me/nft/collection-name-number

export interface ParsedNFTLink {
  name: string
  number: string
  displayName: string
  nftId: string
  collectionName: string
  collectionSlug: string
  giftName: string
}

export function parseNFTLink(nftLink: string): ParsedNFTLink | null {
  try {
    // Pattern: t.me/nft/collection-name-number
    // Examples:
    // - t.me/nft/IonicDryer-7561
    // - https://t.me/nft/IonicDryer-7561
    // - http://t.me/nft/PlushPepe-123
    // - https://t.me/nft/SakuraFlower-73930
    
    const pattern = /t\.me\/nft\/([^-]+)-(\d+)/i
    const match = nftLink.match(pattern)
    
    if (match) {
      const collectionNameRaw = match[1]
      const nftNumber = match[2]
      
      // Generate nftId in format "CollectionName-Number"
      const nftId = `${collectionNameRaw}-${nftNumber}`
      
      // Convert camelCase to Title Case for collection name
      // e.g., "IonicDryer" -> "Ionic Dryer", "SakuraFlower" -> "Sakura Flower"
      const collectionName = collectionNameRaw.replace(/([a-z])([A-Z])/g, "$1 $2").replace(/^./, (c) => c.toUpperCase())
      
      // collectionSlug should be lowercase, no spaces
      const collectionSlug = collectionNameRaw.toLowerCase()
      
      // Generate display name
      const displayName = `${collectionName} #${nftNumber}`
      const giftName = displayName
      
      return {
        name: collectionNameRaw,
        number: nftNumber,
        displayName,
        nftId,
        collectionName,
        collectionSlug,
        giftName,
      }
    }
    
    return null
  } catch (error) {
    console.error("[NFT Parser] Error parsing NFT link:", error)
    return null
  }
}


